export interface ApiResponse<T = any> {
    success: boolean;
    data?: T;
    error?: string;
    message?: string;
}

/**
 * Success response helper
 */
export function successResponse<T>(data: T, message?: string): ApiResponse<T> {
    return {
        success: true,
        data,
        message,
    };
}

/**
 * Error response helper
 */
export function errorResponse(error: string, statusCode = 400): { response: ApiResponse; status: number } {
    return {
        response: {
            success: false,
            error,
        },
        status: statusCode,
    };
}

/**
 * Validate request body with Zod schema
 */
export async function validateRequest<T>(schema: any, data: any): Promise<T | null> {
    try {
        return schema.parse(data) as T;
    } catch (error: any) {
        return null;
    }
}
