import { NextRequest, NextResponse } from 'next/server';
import connectDB from '@/lib/db';
import Order from '@/lib/models/Order';
import Cart from '@/lib/models/Cart';
import Product from '@/lib/models/Product';
import { getUserFromRequest } from '@/lib/middleware';
import { checkoutSchema } from '@/lib/validations';
import { successResponse, errorResponse } from '@/lib/api-response';

// Create order from cart
export async function POST(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const body = await request.json();

        const validated = checkoutSchema.safeParse(body);
        if (!validated.success) {
            const { response, status } = errorResponse(validated.error.issues[0].message);
            return NextResponse.json(response, { status });
        }

        // Get user cart
        const cart = await Cart.findOne({ user: user.userId }).populate('items.product');

        if (!cart || cart.items.length === 0) {
            const { response, status } = errorResponse('Cart is empty');
            return NextResponse.json(response, { status: 400 });
        }

        // Calculate totals
        const subtotal = cart.items.reduce(
            (sum: number, item: any) => sum + item.price * item.quantity,
            0
        );
        const tax = subtotal * 0.1; // 10% tax
        const shippingCost = subtotal > 100 ? 0 : 10; // Free shipping over $100
        const total = subtotal + tax + shippingCost;

        // Create order number
        const orderNumber = `ORD-${Date.now()}-${Math.random().toString(36).substr(2, 9).toUpperCase()}`;

        // Prepare order items
        const orderItems = cart.items.map((item: any) => ({
            product: item.product._id,
            name: item.product.name,
            price: item.price,
            quantity: item.quantity,
            image: item.product.images[0] || '',
        }));

        // Create order
        const order = await Order.create({
            user: user.userId,
            orderNumber,
            items: orderItems,
            shippingAddress: validated.data.shippingAddress,
            paymentMethod: validated.data.paymentMethod,
            subtotal,
            tax,
            shippingCost,
            total,
            notes: validated.data.notes,
        });

        // Update product quantities
        for (const item of cart.items) {
            const product = await Product.findById(item.product);
            if (product && product.trackQuantity) {
                product.quantity -= item.quantity;
                await product.save();
            }
        }

        // Clear cart
        await Cart.findByIdAndDelete(cart._id);

        return NextResponse.json(
            successResponse(order, 'Order placed successfully'),
            { status: 201 }
        );
    } catch (error: any) {
        console.error('Checkout error:', error);
        const { response, status } = errorResponse('Failed to process checkout');
        return NextResponse.json(response, { status });
    }
}

// Get user orders
export async function GET(request: NextRequest) {
    const user = getUserFromRequest(request);
    if (!user) {
        const { response, status } = errorResponse('Authentication required', 401);
        return NextResponse.json(response, { status });
    }

    try {
        await connectDB();

        const orders = await Order.find({ user: user.userId })
            .sort('-createdAt')
            .lean();

        return NextResponse.json(successResponse({ orders }));
    } catch (error: any) {
        console.error('Get orders error:', error);
        const { response, status } = errorResponse('Failed to fetch orders');
        return NextResponse.json(response, { status });
    }
}
